}
*,*::before,*::after{position:relative;box-sizing:border-box
}
html,body{width:100vw;height:100vh;overflow:hidden;background-color:#03080D;color:#eee
}
main{display:-ms-flexbox;display:-webkit-flex;display:-webkit-box;display:flex;height:100%;-ms-flex-pack:center;-webkit-box-pack:center;justify-content:center;-ms-flex-align:center;-webkit-box-align:center;align-items:center
}
.sprite{image-rendering:optimizeSpeed;image-rendering:-moz-crisp-edges;image-rendering:-o-crisp-edges;-ms-interpolation-mode:nearest-neighbor;image-rendering:-webkit-optimize-contrast;image-rendering:pixelated;image-rendering:optimize-contrast;-ms-interpolation-mode:nearest-neighborz
}
@font-face{font-family:'upheaval';src:url("./assets/fonts/upheavtt-webfont.woff2") format("woff2"),url("./assets/fonts/upheavtt-webfont.woff") format("woff");font-weight:normal;font-style:normal
}
